<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')" class="current">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>
		<div class="contents-container">
			<ul class="table mainpage">
				<li class="row props">
					<div class="prop check marR-0">
						<input
							type="checkbox"
							class="checkbox"
							name="member"
							id="select-all"
							v-model="selectAll"
						/>
						<label for="select-all"></label>
					</div>
					<div class="prop index">No.</div>
					<div class="prop">대표이미지</div>
					<div class="prop">카테고리</div>
					<div class="prop" style="width: 335px">타이틀</div>
					<div class="prop">버튼</div>
					<div class="prop">링크</div>
					<div class="prop">수정</div>
				</li>

				<slot v-if="!bannerList">
					<li class="row no-data body3" style="display: block;">
						등록된 글이 없습니다
					</li>
				</slot>
				<slot v-else>
					<li v-for="item in bannerList" :key="item.id" class="row">
						<div class="prop check marR-0">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								:id="item.id"
								:value="item.id"
								v-model="selected"
							/>
							<label :for="item.id"></label>
						</div>
						<div class="prop index">{{ item.id }}</div>
						<div class="prop image">
							<img :src="item.imgPath" width="100" height="70" />
						</div>
						<div class="prop">{{ item.categoryId }}</div>
						<div class="prop justify-flex-start" style="width: 335px">
							{{ item.title }}
						</div>
						<div class="prop">{{ item.btnTitle }}</div>
						<div class="prop">
							<a :href="item.link" target="_blank">
								<Link />
							</a>
						</div>
						<div class="prop">
							<router-link
								:to="{
									name: 'EditBanner',
									params: { id: item.id },
								}"
							>
								<Edit />
							</router-link>
						</div>
					</li>
				</slot>
			</ul>

			<div class="bottom-container">
				<Pagination
					v-if="totalPage !== -1"
					:listRowCt="15"
					:pageLinkCt="5"
					:totalCount="totalPage"
					:url="`/admin/rolling-banner`"
				/>

				<div class="btn-container">
					<button
						class="solid-btn red"
						@click="deleteBanner(data)"
						:disabled="selected.length < 1"
					>
						선택 글 삭제
					</button>
					<router-link :to="{ name: 'AddBanner' }">
						<button class="solid-btn green">
							새 글 등록
						</button>
					</router-link>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { fetchBanner, deleteBanner, fetchBannerTotalCount } from '@/api/banner';
import Pagination from '@/components/common/Pagination';
import Link from '@/assets/icon/Link';
import Edit from '@/assets/icon/Edit';

export default {
	components: { Link, Edit, Pagination },
	created() {
		this.fetchBanner();
		this.fetchBannerTotalCount();
	},
	data() {
		return {
			bannerList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
		};
	},
	computed: {
		selectAll: {
			get: function() {
				return this.bannerList
					? this.selected.length == this.bannerList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.bannerList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchBanner() {
			this.isLoading = true;
			try {
				const { data } = await fetchBanner();
				this.bannerList = data;
			} catch (e) {
				alert(e);
			}
		},
		async fetchBannerTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchBannerTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
		async deleteBanner() {
			this.isLoading = true;
			try {
				console.log(this.selected);
				await deleteBanner(this.selected);
				this.fetchBanner();
				this.selected = [];
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchBanner(this.currentPage - 1);
			}
		},
	},
};
</script>

<style></style>
